<template>
    <section class="MainBooking" id="MainBooking" @scroll="ListenScroll">
        <div 
            class="MainBooking__Header" 
            id="BokNav" 
            :class="{BxAchead: sbar == 'open'}" 
        >
            <HeadNav />
        </div>
        <div class="MainBooking__CardBooking">
            <CoachCard />
        </div>
    </section>
</template>

<script>
    import CoachCard from '@/components/booking/CoachCard';
    import HeadNav from '@/components/booking/TabNav';
    import { scroll } from '@/plugins/directives'
    export default {
        components: {
            CoachCard,
            HeadNav
        },
        directives:{
            scroll
        },
        data: () => ({
            windowWidth: 0,
            windowHeight: 0,
            padding: null,
            items:null
        }),
        mounted() {
            this.$store.dispatch('booking/getallcoach');
            this.$nextTick(function() {
                document.getElementById('MainBooking').addEventListener('scroll', this.handleScroll);
            })
        },
        computed: {
            sbar(){
                return this.$store.state.booking.sBar
            }
        },
        methods: {
            ListenScroll(){
                var scrollPosition = document.getElementById('MainBooking').scrollTop;
                var nav = document.getElementById('BokNav')
                if(scrollPosition >= 150) {
                    nav.classList.add('shrink')
                } else {
                    nav.classList.remove('shrink')
                }
            }
        }
    }
</script>


<style lang="scss">
    @import "~@/assets/scss/booking.scss";
</style>